.album-results {
  &.container {
    padding: 1rem;
    max-width: none !important;
  }
  &.card-deck {
    justify-content: center;
  }
  &.card {
    max-width: 28rem;
  }
  &.card-header {
    position: relative;
    padding: 0;
  }
  &.card-title {
    font-size: 1.6rem;
  }
  &.card-subtitle {
    margin-top: 1rem;
    h2 {
      font-weight: normal;
    }
  }
}

.remove-album {
  &.btn {
    text-align: center;
    position: absolute;
    top: -2.2rem;
    width: 100%;
    z-index: 2;
    right: 0;
    left: 0;
  }
}

.find-album {
  &.btn {
    margin-top: 1rem;
  }
}

img.album-results {
  max-width: 100%;
}
